const initialState = {
  messageList : [],
  activeTab : "1"
}


export default (state = initialState,action) => {
  switch (action.type) {
    case "SET__MESSAGE":
      return {
        messageList : action.payload
      }
    case "CREATE__MESSAGE":
    return {
      messageList : [...state.messageList,action.payload]
    }
    case "EDIT__MESSAGE":
      const editItems = state.messageList.map(item => {
        if(item.id === action.id){
          return { ...item, ...action.payload.content }
        }
        return item
      })

      return { messageList : editItems }
    case "DELETE__MESSAGE":
    return {
      messageList : state.messageList.filter(o => o.id !== action.payload),
    }
    case "SET__TAB":
    return {
      ...state,
      activeTab : action.payload
    }
    default:
      return state
  }
}