export const setMessage = messages => ({
  type : "SET__MESSAGE",
  payload : messages
})

export const createMessage = message => ({
  type : "CREATE__MESSAGE",
  payload : message
})

export const deleteMessage = id => ({
  type : "DELETE__MESSAGE",
  payload : id
})

export const editMessage = (id,text,tag) => ({
  type : "EDIT__MESSAGE",
  id : id,
  payload: { content: {text,tag}}
})


export const setTab = tab => ({
  type : "SET__TAB",
  payload : tab
})
