import React, { Component } from "react";
import MessageListItem from "../contaners/messageListItem";

import { List } from "antd";

class messageList extends Component {
  sendData = () => {
    const { setMessage } = this.props
    // const options = {
    //   method: "GET",
    // };
    // const url = "/message";
    // fetch(url, options)
    //   .then(res => res.json())
    //   .then(d => this.setState({ messageAr : d }));

    const options = {
      method: "GET",
    };
    const url = "/message/";
    fetch(url, options)
      .then(res => res.json())
      .then(d => setMessage(d));
  };

  componentDidMount() {
    this.sendData();
  }

  render() {
    const { messageList } = this.props.messageReducer
    return (
      <List itemLayout="horizontal">
        {messageList.map(message => {
          return (
            <MessageListItem
              key={message.id}
              id={message.id}
              tag={message.tag}
              text={message.text}
              avatar={message.author ? message.author.userpic : ""}
              author={message.author ? message.author.name : ""}
            />
          );
        })}
      </List>
    );
  }
}


export default messageList