import React, { Component } from "react";

import {
  Icon,
  Form,
  Input,
  Button,
  List,
  Modal,
  notification,
  Avatar
} from "antd";

const {TextArea} = Input
const confirm = Modal.confirm;
const key = 'updatable';

export default class MessageListItem extends Component {
  state = {
    collapsed: true,
    visible : false
  };

  openNotification = (message,desc) => {
    notification.open({
      key,
      message: message,
      description: desc,
    });
  };

  showDeleteConfirm = () => {
    const {deleteMessage,id} = this.props
    confirm({
      title: "Вы точно хотите удалить данное сообщение ?",
      content: "Нажимая кнопку ДА вы подтверждаете свое действие",
      okText: "Да",
      okType: "danger",
      cancelText: "Нет",
      onOk() {  
        
        const options = {
          method: "DELETE"
        };
        const url = `/message/${id}`;
    
        fetch(url, options)
          .then(() => deleteMessage(id));
      },
      onCancel() {
        console.log("Отмена");
      }
    });
  }

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    // console.log(e);
    // console.log(this.props);

    const {id,editMessage} = this.props

    this.openNotification("Сообщение","Измененно")
    // const { id } = this.props
    const { tag,text } = this.state

    this.setState({
      visible: false,
    });

    const data = {
      tag,
      text
    }
    
    const options = {
      method: "PUT",
      body: JSON.stringify(data),
      headers: {
        'Content-Type' : "application/json"
      }
    };
    const url = `/message/${id}`;

    fetch(url, options)
      .then(() => editMessage(id,tag,text)
      );
  };

  handleCancel = e => {
    this.setState({
      visible: false
    });
  };

  onChangeHandler = e => {
    const { name,value } = e.target
    this.setState({ [name] : value })
  }

  render() {
    const {tag,text,author,avatar} = this.props

    return (
      <div>
        <List.Item
          actions={[
            <Button onClick={this.showModal}>Редактировать</Button>,
            <Button onClick={this.showDeleteConfirm}>Удалить</Button>
          ]}>
          <List.Item.Meta 
            avatar={<Avatar src={avatar} />}
            title={
             <div>
                <a href="/">{tag}</a> <br/>
                <a href="/">{author}</a>
             </div> 
            } 
            description={text} />
        </List.Item>
        <Modal
            title="Редактирование сообщения"
            cancelText={"Отмена"}
            okText={"Изменить"}
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
          >
            <Form>
              <Form.Item>
                <Input
                  value={this.state.tag}
                  name="tag"
                  onChange={this.onChangeHandler}
                  prefix={
                    <Icon type="tag" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="Напишите свой tag"
                />
              </Form.Item>
              <Form.Item>
                <TextArea 
                  value={this.state.text}
                  name="text"
                  onChange={this.onChangeHandler}
                  rows={4} 
                  placeholder="Напишите текст своего сообщения" />
              </Form.Item>
            </Form>
          </Modal>
      </div>
    );
  }
}
